import "./ListItem.css";
import React from 'react';
import DeleteIcon from '@material-ui/icons/Delete';
import Box from '@material-ui/core/Box';
import { Button } from "@material-ui/core";
import FlipMove from 'react-flip-move';


export default function ListItem(props){

    return(
        <>
        {props.items.map(item=>{return <FlipMove duration={300} easing="ease-in-out"><Box key={item.key} className="list">
        <p>
            <input type="text" value={item.text} onChange={(el)=>props.setUpdate(el.target.value,item.key)}/>
                <span>
                    <Button onClick={()=>props.deleteItem(item.key)}>
                        <DeleteIcon/>
                    </Button>
                </span>
            </p>
            </Box></FlipMove>})}
        </>
    );
}