import React from 'react';
import './App.css';
import ListItem from "./ListItem";

class App extends React.Component{
  constructor(props){
    super(props);
    this.state={
      items:[],
      currentItem:{
        text:'',
        key:''
      }
    }
  }

  handleInput=(el)=>{
    this.setState({
      currentItem:{
        text:el.target.value,
        key:Date.now()
      }
    })
  }

  addItem=(el)=>{
    el.preventDefault();
    const newItem=this.state.currentItem;
    if(newItem.text!=""){
      const items=[...this.state.items,newItem];
      this.setState({
        items:items,
        currentItem:{
          text:"",
          key:""
        }
      })
    }
  }

  deleteItem=(key)=>{
    const filteredItems=this.state.items.filter((val)=>val.key!==key);
    this.setState({items:filteredItems});
  }

  setUpdate=(text,key)=>{
    const filteredItems=this.state.items;
    filteredItems.map((val)=>{
      if(val.key===key)
      val.text=text;
    });
    this.setState({items:filteredItems}); 
  }

  render(){
    return(
      <div className="App">
        <header>
        <form id="to-do-form">
        <input type="text" placeholder="Enter Text" value={this.state.currentItem.text} onChange={this.handleInput}/>
        <button type="submit" onClick={this.addItem}>Add</button>
        </form>
      </header>
      <ListItem setUpdate={this.setUpdate} deleteItem={this.deleteItem} items={this.state.items}></ListItem>
      </div>
    );
  }
}

export default App;